

import { put, call, select } from 'redux-saga/effects';
import {
    serverSuccess
} from '../actions/common'
import Http from '../services/http'
export default function* homeRequested({request}) {
    try {
        const endpointToSearchModule = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY+ 'read/';
        const session = yield select(state => state.SESSION);
        yield put({type: "SERVER_PENDING"});
        const apiCall = yield call(Http.post, endpointToSearchModule, request, session.token)
        if(!apiCall.isError){
            yield put(serverSuccess(apiCall))
        } else{
            yield put({type: "SERVER_FAILED"})
        }
    } catch (err) {
        console.log(err)
        yield put({type: "SERVER_FAILED"});
    }
}
