import {all, takeEvery} from 'redux-saga/effects';
import {LOGIN_REQUEST, CHANGE_PAGE_REQUEST, SERVER_REQUEST} from "../actions/common";
import login from './login';
import uModuleRequested from './uModule'
import uModuleRequestedTable from "./uModuleTable";
import uModuleRequestedCUD from "./uModuleCUD";
import {UMODULE_REQUEST, UMODULE_REQUEST_CUD, UMODULE_REQUEST_TABLE} from "../actions/uModule";
import pageChangeRequest from "./changePage";
import homeRequested from "./serverRequestHome";

export default function* root() {
    yield all([
        takeEvery(LOGIN_REQUEST, login),
        takeEvery(UMODULE_REQUEST, uModuleRequested),
        takeEvery(UMODULE_REQUEST_TABLE, uModuleRequestedTable),
        takeEvery(UMODULE_REQUEST_CUD, uModuleRequestedCUD),
        takeEvery(CHANGE_PAGE_REQUEST, pageChangeRequest),
        takeEvery(SERVER_REQUEST, homeRequested)
        ]);
}


