import React, {Component} from "react";
import {map} from "lodash";
import {Link, withRouter} from "react-router-dom";
import {connect} from "react-redux";

class  NavBar extends Component{
    constructor(props) {
        super(props);
        this.state = {
            showing: true
        }
    }

    handleHide(){
        this.setState((prevState)=>{
            return {
                showing: !prevState.showing
            }
        })
    }

    render() {
        const routes = [
            "Home",
            "Ap",
            "Ventas",
            "Usuarios",
            "Promociones",
            "Productos"]
        return (
            <div className={'menuDash'}>
                    <div className="container-fluid col justify-content-center align-content-center p-0 m-0">

                        <div className="row col justify-content-end align-content-end ">
                            <div className=" row col-3 justify-content-end align-content-end ">
                                <i className="bi bi-list text-white fs-1 col" onClick={e=>this.handleHide(e)}></i>
                            </div>
                        </div>

                        <div className="row">
                            <ul className={"row justify-content-center align-content-center "}>
                                {map(routes, (itm, key) => {
                                   return this.state.showing ? (<div/>) :
                                        (<li key={key} className={"col-sm-4 row justify-content-center"}>
                                            <Link key={key} className={'m-3 fw-bold  text-decoration-none col justify-content-center col row justify-content-center align-content-center'} to={itm === 'Home'? '' : itm}>
                                                <button className={'row btn rounded-2 bg-danger text-white fw-bold '}>
                                                    {itm}
                                                </button>
                                            </Link>
                                        </li>)
                                    })
                                }
                            </ul>
                        </div>
                    </div>
            </div>
        )
    }
}

export default connect(
    state => ({
        profile: state.SESSION.profile,
    })
)(withRouter(NavBar))
