import {UMODULE_FAILED, UMODULE_PENDING, UMODULE_REQUEST, UMODULE_SUCCESS} from "../actions/uModule";

export default function UMODULE(state =
                                    {success: false, sending: false,
                                        error: false},action) {
    switch (action.type) {
        case UMODULE_REQUEST:
            return {
                ...state, sending: true, error: false, success: false
            };
        case UMODULE_SUCCESS:
            return {
                ...state, success: true, sending: false, error: false, payload: action.payload
            };
        case UMODULE_FAILED:
            return {
                ...state, success: false,  sending: false, error: true
            }
        case UMODULE_PENDING:
            return {
                ...state, success: false,  sending: true, error: false
            }
        default:
            return state;
    }
}
