export default class Http {
    static async get(url, init) {
        const response = await fetch(url, init);
        try {
            return response.json();
        } catch (parseError) {
            return {error: 'An error ocurred on the get', response};
        }
    }

    static async post(url, body, token) {
        const response = await fetch(url, {
            method: 'post',
            body: JSON.stringify(body),
            headers: {
                'simple': true,
                'content-type': 'application/json',
                'token': token
            }
        });
        try {
            return response.json();
        } catch (parseError) {
            return {isError: true, message: 'An error ocurred on the post', response};
        }
    }

    static async login(url,body) {
        const response = await fetch(url, {
            method: 'post',
            body:  JSON.stringify(body),
            headers: {
                'content-type': 'application/json'
            }
        });
        try {
            return response.json();
        } catch (parseError) {
            return {error: 'An error ocurred on the login', response};
        }
    }


}
