import React, {Component} from 'react'
import {filter, find, map, sum, size} from "lodash";
import Loading from "../Loading";

const getFromApi = async (url, body, token) => {
    const response = await fetch(url, {
        method: 'post',
        body: JSON.stringify(body),
        headers: {
            'content-type': 'application/json',
            'token': token
        }
    });
    try {
        return {isError: false, message: response.json()}
    } catch (parseError) {
        return {isError: true, message: {label: "No se encontro nada", value: undefined}};
    }


}

class SelectMultiple extends Component {
    constructor(props) {
        super(props);
        this.state = {
            options: [],
            values: []
        }

    }
     async componentDidMount() {
        if(this.props.value){
            const source = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + this.props.extra[3];
            const response = await getFromApi(source, {_id: this.props.value}, this.props.token).then((itm) => {return (itm.message)}, () => {return {label: "No encontrado", value: 'default'}})
            await this.setState( () => {
               return {values: response.message}
            })
        }
    }

    async handleSearch(e){
        const source = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + this.props.extra[3]
        if(e.target.name === 'searchInput' && e.target.value.length > 3){
            const response =await  getFromApi(source, {[this.props.extra[4]]: e.target.value}, this.props.token).then(
                (itm)=>{return (itm.message)},
                ()=>{return {label: "No encontrado", value: 'default'}})
            this.setState({options: response.message})
        }}


    handleSelect(e){
        this.setState({[e.target.name]: e.target.value})
    }

    handleChange(){
        if(this.state.selectInput !== 'default'){
            this.setState(
                prevState => {
                    return {values:  [...prevState.values, prevState.options[prevState.selectInput]]}
                })
            this.props.onChange(this.props.name, [...map(this.state.values, (itm)=>{return itm._id}), this.state.options[this.state.selectInput]._id])
            if(this.props.extra[6] === 'add'){
                this.props.onChange(this.props.extra[5], sum(map(this.state.values, (itm)=>{return itm.value})) + (this.state.options[this.state.selectInput].value))
            } else {
                let total = this.props.getter(this.props.extra[5]) || 0;
                total = total  * this.state.options[this.state.selectInput].value
                this.props.onChange(this.props.extra[5], total);
            }

       }

    }

    handleDelete(e){
        this.setState(prevState =>{
                const newValues = filter(prevState.values, (itm, ind)=>{
                    return ind!== e
                })
            this.props.onChange(this.props.name, map(newValues, (itm)=>{return itm._id}));

            if(this.props.extra[6] === 'add') {
                this.props.onChange(this.props.extra[5], sum(map(newValues, (itm) => {
                    return itm.value
                })))
            } else {
                let total = this.props.getter(this.props.extra[5]) || 0;
                total = total / prevState.values[e].value

                this.props.onChange(this.props.extra[5], total)

            }
                return {
                    values: newValues
                }
            }

        )

    }

    render() {
        return (
            <div className={"row justify-content-center m-2"}>
                <div className="row fs-3">
                    {this.props.label}
                </div>
                <div className="row justify-content-between">
                    <input className={" col-sm-4  bg-dark text-white"} type="text" name="searchInput" onChange={(e)=>this.handleSearch(e)}/>
                    <select  className={"col-sm-4  bg-dark text-white"} name="selectInput" onChange={(e)=>this.handleSelect(e)}>
                        <option value={'default'} >Seleccione</option>
                        {map(this.state.options, (opt, ind)=>{
                            return <option value={ind} key={ind}> {map(this.props.extra[4], (itm)=>{return ('  ' +opt[itm])})}</option>
                        })}
                    </select>
                    <button onClick={()=>this.handleChange()} className={"btn btn-dark text-white col-sm-3 border-danger m-1 p-1"}> Agregar</button>

                </div>
                <div className="row">
                    {this.state.values.length > 0 ?
                        <div className="bg-dark border-danger">
                            {map(this.state.values, (value, index)=>{

                                return (
                                    <div key={index} className="row">
                                        <div className="col-10 text-white">
                                            {map(this.props.extra[4], (itm)=>{return (

                                                '  ' +value[itm])})}
                                        </div>
                                        <div className="col-2 justify-content-end">
                                            <button onClick={()=>this.handleDelete(index)} className="btn btn-danger fw-bold">X</button>
                                        </div>

                                    </div>


                                )
                            })}

                        </div> : <div/>

                    }
                </div>

            </div>
        );
    }
}

class SelectMultipleCustom extends Component{
    constructor(props) {
        super(props);
        this.state = {
            values:  [],
            options: [],
            optionsSecond: []
        }
    }

    async componentDidMount() {
        if (this.props.value !== undefined) {
            const sourceProduct = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + this.props.extra[3]
            const sourceAp = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + this.props.extra[7] + '/read'
            let idProduct = [];let idAp = []
            map(this.props.value, (itm) => {idProduct = [...idProduct, itm.product];idAp = [...idAp, itm.ap]})
            const responseProduct = await getFromApi(sourceProduct, {_id: idProduct}, this.props.token).then((itm) => {return (itm.message)}, () => {return {label: "No encontrado", value: 'default'}})
            const responseAp = await getFromApi(sourceAp, {_id: idAp}, this.props.token).then((itm) => {return (itm.message)}, () => {return {label: "No encontrado", value: 'default'}})
            let state = []
            for (const i in this.props.value) {state =[...state,{product: find(responseProduct.message, (obj)=> {return obj._id === this.props.value[i].product}), ap: find(responseAp.message, (obj)=> {return obj._id === this.props.value[i].ap}), value: this.props.getter(this.props.extra[1])[i].value}]}
            this.setState( {values: state})
        }
    }

    async handleSearch(e){
        const source = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + this.props.extra[3]
        if(e.target.value.length > 3){
            this.setState({searching: true})
            const response =await  getFromApi(source, {[this.props.extra[4]]: e.target.value}, this.props.token).then(
                (itm)=>{return (itm.message)},
                ()=>{return {label: "No encontrado", value: 'default'}})
            this.setState({options: response.message, searching: false})
        }}
   async handleFirstSelect(e){
        const source = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + this.props.extra[7] + '/read'
        const response =await getFromApi(source, {_id: this.state.options[e.target.value][this.props.extra[7]]}, this.props.token).then(
                    (itm)=>{return (itm.message)},
                    ()=>{return {label: "No encontrado", value: 'default'}})
        this.setState({optionsSecond: response.message, [e.target.name]: e.target.value})
    }
    handleSecondSelect(e){
        this.setState({[e.target.name]: e.target.value})
    }

    handleAdd() {
        //TODO: fix empty
        if (this.state.options.length > 0) {
            this.setState((prevState) => {

                this.props.onChange(this.props.extra[5], sum(map(prevState.values, (itm) => {
                    return (itm.value * itm.ap.value)
                })) + (this.state.optionsSecond[this.state[this.props.extra[7]]].value * this.state.options[this.state[this.props.extra[8]]].value))
                this.props.onChange(this.props.extra[1], map([...prevState.values, {
                    product: this.state.options[this.state[this.props.extra[8]]],
                    ap: this.state.optionsSecond[this.state[this.props.extra[7]]],
                    value: (this.state.optionsSecond[this.state[this.props.extra[7]]].value * this.state.options[this.state[this.props.extra[8]]].value)
                }], (itm) => {
                    return {product: itm.product._id, ap: itm.ap._id, value: itm.value}
                }))


                return {
                    values: [...prevState.values, {
                        [this.props.extra[7]]: this.state.optionsSecond[this.state[this.props.extra[7]]],
                        [this.props.extra[8]]: this.state.options[this.state[this.props.extra[8]]],
                        value: this.state.options[this.state[this.props.extra[8]]].value
                    }

                    ]
                }
            })
        }

    }

    handleValueChange(e){
        //here

        this.setState(
            (prevState) =>{
                let state = prevState.values
                console.log(e.target.value)
                if (size(e.target.value) === 0 ){
                    state[e.target.name].value = ''
                }
                else
                {
                state[e.target.name].value = e.target.value
                this.props.onChange(this.props.extra[5], sum(map(state, (itm)=>{return (itm.value * itm.ap.value)})) )
                this.props.onChange(this.props.extra[1], map(state, (itm)=>{
                    return {product: itm.product._id, ap: itm.ap._id, value: itm.value}
                }))}


                return {
                    values: state

                }
            }
        )



    }

    handleDelete(e){
        //here
        this.setState(prevState =>{
                const newValues = filter(prevState.values, (itm, ind)=>{
                    return ind!== e
                })
                this.props.onChange(this.props.extra[5], sum(map(newValues, (itm) => {
                    return (itm.value * itm.ap.value)
                })))
                this.props.onChange(this.props.extra[1], map(newValues, (itm)=>{
                    return {product: itm.product._id, ap: itm.ap._id, value: itm.value}
                }))
                return {
                    values: newValues
                }
            }

        )


    }


    render(){
        return (
            <div className="">
                <div className={""}>
                    <div className="col-sm-4  row">
                        <input type="text" placeholder="Escribir Para Buscar" name="Buscador de productos" onChange={(e)=>this.handleSearch(e)}/>
                    </div>
                    <div className="col-sm-4 row">
                        <select name={this.props.extra[8]} placeholder="seleccione" onChange={(e)=>this.handleFirstSelect(e)}>
                            <option value="default">Seleccione el Producto</option>
                            {this.state.options.length > 0 ?
                                map(this.state.options, (itm, ind)=>{
                                    return (
                                        <option value={ind} key={ind}> {itm.name} - {itm.category}</option>
                                    )
                                }) : <option value="default">Seleccione el Producto</option>}
                        </select>
                    </div>
                    <div className="col-sm-4 row">
                        <select name={this.props.extra[7]} onChange={(e)=>this.handleSecondSelect(e)}>
                            <option value="default">Seleccione el Ap</option>
                            {this.state.optionsSecond.length > 0 ?
                                map(this.state.optionsSecond, (itm, ind)=>{
                                    return (
                                        <option value={ind} key={ind}> {itm.label} - {itm.value}</option>
                                    )
                                }) : <option value="default">Seleccione el Ap</option>}
                        </select>
                    </div>
                    <div className="col-sm-2 row">
                        <button className="btn btn-danger " onClick={()=>this.handleAdd()}>Agregar</button>
                    </div>
                </div>
                <div className="">
                    {this.state.values.length > 0 ?
                    map(this.state.values, (itm, ind)=> {
                            return (
                                <div className="row" key={ind}>
                                    <div className="col-sm-4">
                                        {itm.product.name} {itm.value} x {itm.ap.value} - {itm.ap.label} Total: {itm.value * itm.ap.value}
                                    </div>
                                    <div className="col-sm-3 row">
                                        <input value={itm.value } type={"number"} name={ind} id="" onChange={(e)=>this.handleValueChange(e)}/>
                                    </div>
                                    <div className="col row justify-content-end">
                                        <button className="btn btn-close bg-danger" onClick={()=>this.handleDelete(ind)}></button>
                                    </div>
                                </div>
                            )
                        }) : this.state.searching ? <Loading label={'Cargando'}/> : <div/>}
                </div>
            </div>


        )
    }
}
export {SelectMultiple, SelectMultipleCustom}
