import React, {Component} from 'react';
import {connect} from "react-redux";
import {uModuleRequest} from "../../../store/actions/uModule";
import {withRouter} from "react-router-dom";
import TableList from "../TableList";
import AddNew from "../FormCreator/index";
import {Error404} from "../index";
import Loading from "../Loading";

class ModuleParser extends Component {
    constructor(props) {
        super(props);
        this.state = {
            page: '0',
            skip: 0,
            limit: 0,
            valuesUpdate: {},
        }
    }
    onChangePage(skip){
        this.setState(prevState=> {
            return {skip: skip === 0 ? 0 : skip < 0 ? 0 : prevState.skip+skip}
        })
        this.props.readTableInformation({modules: this.props.module, body: {...this.state.valuesUpdate,skip: skip} }
        )

    }

    componentDidMount() {
        this.props.readTableInformation({modules: this.props.module, body: {skip: this.state.skip} }
        )
    }

    handlePage(index,mode, values, skip){
        this.setState({page: index,mode: mode,  valuesUpdate: values, skip: skip})
        this.props.readTableInformation({modules: this.props.module, body: {...values, skip: skip} })
    }

    render() {
        return (
            <div className={"row  justify-content-center bg-black text-white"}>
                <div className="row justify-content-end p-4">
                    <div className="row col-12 mt-3">
                        <div className="col-6 ">
                            <button className="btn btn-sm btn-danger fs-3" onClick={()=> this.handlePage('1','create', {})}> Nuevo</button>
                        </div>
                        <div className="col-6 ">
                            <button className="btn btn-sm btn-danger fs-3" onClick={()=> this.handlePage('0','read', {})} >Tabla</button>
                        </div>
                    </div>
                    <div className={"row justify-content-center align-content-center p-0 m-0"}>
                        {(this.props.table.error ? <Error404 label={'Error'}/> :
                            this.props.table.sending ? <Loading label={'Cargando'}/> :
                                                        (!this.props.table.success || this.props.table.payload[this.props.module] === undefined) ? <Error404 label={'Error'}/> :
                                                            (this.state.page === '0' ?
                                                                        <div className={"row col min-vh-100 bg-dark justify-content-between align-content-between mt-5 pb-5 p-0"} key={this.props.module}>

                                                                            <TableList
                                                                                        prevState={this.state.valuesUpdate}
                                                                                        handlePageCallback={(values)=>this.handlePage('0', 'read', values, 0)}
                                                                                        header={this.props.header}
                                                                                        onUpdateValues={(values)=>this.handlePage('1', 'update', values)}
                                                                                        onDeleteValues={(values)=>this.handlePage('1', 'delete', values)}
                                                                                        modules={this.props.module}/>
                                                                            <div className="row col p-0 m-0 pt-2">
                                                                                <div className="col-12 bg-dark row justify-content-center align-content-center">
                                                                                    <div className="row col-12 justify-content-center align-content-center">
                                                                                        <div className="col-2 text-white fw-bold">
                                                                                            <button className="btn btn-danger " onClick={()=>this.onChangePage(0)}> {`<<`}</button>
                                                                                        </div>
                                                                                        <div className="col-2">
                                                                                            <button className="btn btn-danger" onClick={()=>this.onChangePage(this.state.skip -1 > 0 ? this.state.skip -1 : 0 )}> {`<`} </button>
                                                                                        </div>
                                                                                        <div className="col-2">
                                                                                            {this.state.skip}
                                                                                        </div>
                                                                                        <div className="col-2">
                                                                                            <button className="btn btn-danger" onClick={()=>this.onChangePage(1)}> {`>`} </button>
                                                                                        </div>
                                                                                        <div className="col-2">
                                                                                            <button className="btn btn-danger" onClick={()=>this.onChangePage(5)}> {`>>`}  </button>
                                                                                        </div>

                                                                                    </div>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                        :
                                                                        <div className={"row min-vh-100 bg-dark"}>
                                                                            <AddNew
                                                                                modules={this.props.module}
                                                                                valuesUpdate={this.state.valuesUpdate ? this.state.valuesUpdate : {}}
                                                                                mode={this.state.mode}
                                                                                handlePage={()=>this.handlePage('0', 'read', {})}
                                                                                form={this.props.form}
                                                                            />
                                                                        </div>
                                                                            ))}
                                    </div>
                </div>
            </div>
        );
    }
}


export default connect(
    state => ({
        session: state.SESSION,
        table: state.UMODULE

    }),
    dispatch => ({
        readTableInformation:(payload)=> dispatch(uModuleRequest(payload))
    }))(withRouter(ModuleParser))
