
import React, {Component} from "react";
import {connect} from "react-redux";
import {uModuleRequest} from "../../../store/actions/uModule";
import {withRouter} from "react-router-dom";
import { map} from "lodash";
import {Error404} from "../index";
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import HeaderSearch from "./HeaderComponent";


const getFromApi = async (url, body, token) => {
    const response = await fetch(url, {
        method: 'post',
        body: JSON.stringify(body),
        headers: {
            'content-type': 'application/json',
            'token': token
        }
    });
    try {
        return {isError: false, message: response.json()}
    } catch (parseError) {
        return {isError: true, message: {label: "No se encontro nada", value: undefined}};
    }


}
class Combo extends Component{
    constructor(props) {
        super(props);
        this.state = {
            value: [],
            fetch: []
        }
    }

   async componentDidMount() {
          const apiCall =  await getFromApi((this.props.endpoint),{_id: this.props.body}, this.props.token).then(
              (itm)=>{return (itm.message)},
              ()=>{return {label: "No encontrado", value: 'default'}})
                if(apiCall.message.length === 0) {
                    this.setState((prevState)=>{return {value: [...prevState.value, 'Error']}})}
                else {
                    this.setState(() => {return {value: apiCall.message}})
                }
    }
    render(){




        return (
            this.state.value.length > 0 ?
            <div className={"row col justify-content-center align-content-center"}>
                {map(this.state.value, (itm)=>{
                    return <div>
                        {itm.label} - {itm.value}
                    </div>


                })}
            </div> : <div>Cargando</div>
        )

    }
}

class TableList extends Component{
    constructor(props) {
        super(props);
        this.state = {
            body: null,
        }
    }





render(){
    return (
        <div>
        {
            this.props.table.sending ?
                <div>Loading</div> :
                (
                    this.props.table.success || this.props.table.payload[this.props.modules] ? (
                        <div className={"row bg-danger  m-0 p-0"} >
                            <div className="row p-0 m-0 pt-1">
                                    <HeaderSearch header={this.props.header} modules={this.props.modules} prevState={this.props.prevState} readTableInformationCallback={this.props.handlePageCallback} />
                            </div>

                            <div className="row justify-content-center">
                                <Table className="text-white table-bordered fs-6 p-0 m-0 ">
                                    <Thead >
                                        <Tr>
                                            {map(this.props.header, (triplet, key)=>{
                                                return (
                                                    <Th className="text-white bg-dark" key={key}>
                                                        <div>{triplet[1]}</div>
                                                    </Th>
                                                )
                                            })}
                                        </Tr>
                                    </Thead>
                                    <Tbody >
                                        {map(this.props.table.payload[this.props.modules].message, (item, key)=>{
                                                return(<Tr className={"col justify-content-center"}>{map(this.props.header ,(head)=>{
                                                    if (item[head[0]] !== undefined || head[0] === 'index' || head[0] === "update" || head[0] === "delete"){
                                                        switch(head[0]){
                                                            case "index": return <Td className="">{key}</Td>
                                                            case "createdAt": return <Td className="">{new Date(item[head[0]]).toISOString().toString().split('T')[0]}</Td>
                                                            case "updatedAt": return <Td className="">{new Date(item[head[0]]).toISOString().toString().split('T')[0]}</Td>

                                                            default:
                                                            {
                                                                switch (head[2]){
                                                                    case 'checkbox': return <Td ><i
                                                                        className="bi bi-gem" style={item[head[0]] ? {color: 'black'} : {color: 'red'}}></i></Td>
                                                                    case 'combo': return <Td className=""> {
                                                                        <Combo
                                                                            endpoint={process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY +head[0]+'/read' }
                                                                            body={item[head[0]]}
                                                                            token={ this.props.token}
                                                                        />


                                                                    } </Td>
                                                                    default: return <Td className="col justify-content-center">{item[head[0]]}</Td>
                                                                }
                                                            }



                                                        }
                                                    }})
                                                }
                                                    <Td className="col justify-content-center  "><button className="btn btn-dark text-white " onClick={()=>this.props.onUpdateValues(item)}>Modificar</button> </Td>
                                                    <Td className=" col justify-content-center "><button className="btn btn-dark text-white " onClick={()=>this.props.onDeleteValues(item)}>Borrar</button> </Td>
                                                </Tr>)
                                            }
                                        )}

                                    </Tbody>
                                </Table>
                            </div>

                         </div>
                            ) : <Error404/>
                )
    }
    </div>)
}
}


export default connect(
    state => ({
        table: state.UMODULE,
        token: state.SESSION.token
    }),
    dispatch => ({
        readTableInformation:( payload)=> dispatch(uModuleRequest(payload))
    })
)(withRouter(TableList))

