import { put, call, select } from 'redux-saga/effects';
import Http from '../services/http'
import {uModuleFailedCud, uModuleSuccessCud} from "../actions/uModule";


export default function* uModuleRequestedCUD({payload}) {

    try {
        const session = yield select(state => state.SESSION);
        const body = {...payload.args}
        const cud = payload.mode === 'delete' ? '/delete/' : (body._id ? '/update' : '/create/')
        const endpointToPost = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + payload.modules + cud
        const tableInformation = yield call(Http.post, endpointToPost , body, session.token)
        if (!(tableInformation.isError)) {
            yield put(uModuleSuccessCud({[payload.modules]: tableInformation}))
        } else {
            yield put(uModuleFailedCud(tableInformation.message ? {[payload.modules]: tableInformation} : {[payload.modules]: 'Error Con el Servidor'}));
        }
    }
    catch (e) {
        console.log(e)
        yield put(uModuleFailedCud('Error Con el Servidor'));
    }
}
