import React, {Component} from 'react';
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import Chartx from "./Chart";
import {includes, map} from "lodash";
import {serverRequest} from "../../../store/actions/common";
import Loading from "../../common/Loading";
import {Error404} from "../../common";
import {SelectMultiple} from "../../common/Components/SelectMultiple";


class Home extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.request({})
    }
    cleanHandler(){
        this.setState({
            type: undefined,
            confirmed: undefined,
            accepted: undefined,
            from: undefined,
            to: undefined,
            otd: undefined,
            message: undefined
        })
        this.props.request({})
    }
    inputHandler(e){
        this.setState({[e.target.name]: e.target.value })
    }
    stateHandler(name, value){
        this.setState({[name]: value})
    }
    checkBoxHandler(e){
        this.setState({[e.target.name]: e.target.value === 'on'})
    }
    buttonHandler(e){
        e.preventDefault(e)
        this.props.request({
            type:this.state.type,
            from: this.state.from,
            to: this.state.to,
            confirmed: this.state.confirmed,
            accepted: this.state.accepted ,
            ticketNum: this.state.otd ,
            message: this.state.message
        })
    }
    render() {

        if(this.props.home.sending){
            return (
                <Loading label={'Cargando Papurris'}/>
            )
        }

        if(this.props.home.error){
            return (
                <Error404/>
            )
        }

        if(this.props.home.success){
            const permissionAdmin = includes(this.props.session.profile.profile.role, 'admin')

            return (
                <div className={'container-fluid  pt-5 mt-5'}>
                    <div className="row">
                        {
                            permissionAdmin ?
                                <div className="row">

                                    <div className="row col p-2 m-2 bg-white text-black fw-bold fs-1">
                                        Mensaje del Dia:
                                        <div className="col row justify-content-center align-content-center">
                                        {this.props.home.request.message.dayMessage}
                                        </div>
                                    </div>

                                    <div className="row justify-content-center align-content-center">
                                        <div className="row m-5 justify-content-center align-content-center text-white fw-bold">
                                            Ventas por vendedor
                                        </div>

                                        <div className="row col-sm-6 justify-content-center align-content-center bg-dark">
                                            <Chartx
                                                id={'chart-bar-vendedores'}
                                                className={'chart-canvas chartjs-render-monitor'}
                                                type={'pie'}
                                                labels={
                                                    map(this.props.home.request.message.chart, (itm, key)=>{return (key+' --'+ itm.ventas+' $'+itm.total)})


                                                }
                                                data={map(this.props.home.request.message.chart, (itm)=>{return (itm.total)})}
                                            />
                                        </div>

                                    </div>
                                    <div className="row justify-content-center align-content-center">
                                        <div className="row m-5 justify-content-center align-content-center text-white fw-bold">
                                            Ventas por Producto
                                        </div>

                                        <div className="row col-sm-6 justify-content-center align-content-center bg-dark">
                                            <Chartx
                                                id={'chart-bar-vendedores'}
                                                className={'chart-canvas chartjs-render-monitor'}
                                                type={'pie'}
                                                labels={
                                                map(this.props.home.request.message.products, (itm)=>{return (Object.keys(itm)+' -- '+itm[Object.keys(itm)].amount + ' $'+ itm[Object.keys(itm)].value)})
                                                }
                                                data={
                                                map(this.props.home.request.message.products, (itm) =>
                                                {return itm[Object.keys(itm)].value})}
                                            />
                                        </div>

                                    </div>
                                    <div className="row justify-content-center align-content-center">
                                        <div className="col justify-content-center align-content-center row bg-dark text-white fw-bold mt-5 pt-5 mb-5 pb-5">
                                            <div className="row col-10 justify-content-center align-content-center mt-3">
                                                <div className="row">
                                                    Tipo
                                                </div>
                                                <input type="text" name={'type'} onChange={(e)=>this.inputHandler(e)}/>
                                            </div>
                                            <div className="row col-10 justify-content-center align-content-center mt-3">
                                                <div className="row">
                                                    Desde
                                                </div>
                                                <input type="date" name={'from'} onChange={(e)=>this.inputHandler(e)}/>
                                            </div>
                                            <div className="row col-10 justify-content-center align-content-center mt-3">
                                                <div className="row">
                                                    Hasta
                                                </div>
                                                <input type="date" name={'to'} onChange={(e)=>this.inputHandler(e)}/>
                                            </div>

                                            <div className="row col-10 justify-content-center align-content-center mt-3">
                                                <div className="row">
                                                    Confirmado
                                                </div>
                                                <input type="checkbox" name={'confirmed'} onChange={(e)=>this.checkBoxHandler(e)}/>
                                            </div>

                                            <div className="row col-10 justify-content-center align-content-center mt-3">
                                                <div className="row">
                                                    Aceptado
                                                </div>
                                                <input type="checkbox" name={'accepted'} onChange={(e)=>this.checkBoxHandler(e)}/>
                                            </div>

                                            <div className="row col-10 justify-content-center align-content-center mt-3">
                                                <div className="row">
                                                    Filtrar por promocion
                                                </div>
                                                <SelectMultiple type={'text'} value={undefined} name={'ticketName'} onChange={(name, value) => this.stateHandler(name, value)} extra={['selectMultiple', 'promotion', 'Promociones', 'promotion/read',['label', 'value'], ' ', ' ']} token={this.props.token} getter={(a)=>{return this.state[a]}}/>
                                            </div>

                                            <div className="row col-10 mt-5 justify-content-center align-content-center mt-3">
                                                <div className="row">
                                                    Mensaje del Dia
                                                </div>
                                                <input type="text" name={'message'} onChange={(e)=>this.inputHandler(e)}/>
                                            </div>
                                            <div className="row col-10 justify-content-around align-content-center  m-5">
                                                <div className="row col-5">
                                                    <button className={"btn btn-danger"} onClick={(e)=>this.buttonHandler(e)}>Enviar</button>
                                                </div>
                                                <div className="row col-5">
                                                    <button className={"btn btn-danger"} onClick={()=>this.cleanHandler()}>Limpiar</button>
                                                </div>
                                            </div>
                                            {this.props.home.request.message.staticsData ?
                                            <div className="row col-sm-12 justify-content-center align-content-center m-5">
                                                <div className="row col-12">
                                                   <div className='fs-5 col  p-1'> Total: </div>
                                                    <div className="col row justify-content-center align-content-center">
                                                    {this.props.home.request.message.staticsData.total}
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    <div className='fs-5 col p-1'> Total Efectivo: </div>
                                                    <div className="col row justify-content-center align-content-center">
                                                        {this.props.home.request.message.staticsData.totalEfectivo}
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    <div className='fs-5 col  p-1'> Total Tarjeta: </div>
                                                    <div className="col row justify-content-center align-content-center">
                                                    {this.props.home.request.message.staticsData.totalTarjeta}
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    <div className='fs-5 col  p-1'> Total Sin Confirmar: </div>
                                                    <div className="col row justify-content-center align-content-center">
                                                    {this.props.home.request.message.staticsData.totalSinConfirmar}
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    <div className='fs-5 col  p-1'> Total Sin Avisar: </div>
                                                    <div className="col row justify-content-center align-content-center">
                                                    {this.props.home.request.message.staticsData.totalNoOtd}
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    <div className='fs-5 col p-1'> Total Avisado: </div>
                                                    <div className="col row justify-content-center align-content-center">
                                                    {this.props.home.request.message.staticsData.totalOtd}
                                                    </div>
                                                </div>
                                            </div> : <div/>}




                                        </div>
                                    </div>

                                    <div className="row justify-content-center align-content-center">
                                        <div className="col justify-content-center align-content-center row bg-dark text-white fw-bold mt-5 pt-5 mb-5 pb-5">
                                            <div className="fs-2"> Detalle de Tarjetas</div>
                                            {this.props.home.request.message.cards ?

                                            map(this.props.home.request.message.cards, (card, ind)=>{
                                                return <div className="pt-3 row ">
<div className="col" >{ind}</div>
                                                    {card}


                                                </div>
                                            }) : <div/>}
                                        </div>


                                    </div>

                                </div> :
                                <div className="row">
                                    <div className="col-sm-6">
                                        <div className="row col-10 p-5 m-5 bg-white text-black fw-bold fs-1">
                                            Mensaje del Dia: {this.props.home.request.message.dayMessage}
                                        </div>
                                    </div>
                                    <div className="row col-sm-6">

                                        <div className="row col-sm-8 bg-dark">
                                            <div className="row fs-2  fw-bold text-white">
                                                Ventas Del Dia
                                            </div>
                                            <Chartx
                                                id={'chart-bar-vendedores'}
                                                className={'chart-canvas chartjs-render-monitor'}
                                                type={'pie'}
                                                labels={map(this.props.home.request.message.chart, (itm, key)=>{return (key+' --'+ itm.ventas+' $'+itm.total)})}
                                                data={map(this.props.home.request.message.chart, (itm)=>{return (itm.total)})}
                                            />
                                        </div>
                                    </div>



                                </div>


                        }
                    </div>
                </div>
            );
        }


    }
}

export default connect(
    state => ({
        token: state.SESSION.token,
        session: state.SESSION,
        home: state.HOME
    }),
    dispatch =>({
        request: (payload)=> dispatch(serverRequest(payload))}
))(withRouter(Home))

