import React, {Component} from 'react';
import {connect} from "react-redux";
import {resetModuleCud, uModuleRequestCud} from "../../../store/actions/uModule";
import {withRouter} from "react-router-dom";
import success from "../../../images/success.png"
import {includes, map} from "lodash";
import {Error404} from "../../common";
import {SelectMultiple, SelectMultipleCustom} from "../Components/SelectMultiple";

const getFromApi = async (url, body, token) => {
    const response = await fetch(url, {
        method: 'post',
        body: JSON.stringify(body),
        headers: {
            'content-type': 'application/json',
            'token': token
        }
    });
    try {
        return {isError: false, message: response.json()}
    } catch (parseError) {
        return {isError: true, message: {label: "No se encontro nada", value: undefined}};
    }


}


class ComponentCustom extends Component{
    constructor(props) {
        super(props);
        this.state={
            options: {}
        }

    }



    handle(e){
        this.props.onChange(e.target.name, e.target.value)
    }
    handleArray(e){
        const value =includes(e.target.value, ',') ? e.target.value.split(',') : e.target.value.split(' ')
        this.props.onChange(e.target.name, value)
    }
    handleSelect(e){
        this.props.onChange(this.props.name, e.target.value)
    }

    handleCheckBox(e){
        this.props.onChange(this.props.name, e.target.checked)
    }

    async handleSearchSelect(e){
        const endpoint = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + this.props.extra[3];
        if(e.target.name === 'searchInput' && e.target.value.length > 3){
            const body = {[this.props.extra[4]]: e.target.value}
            const response =await  getFromApi(endpoint, body, this.props.token).then(
                (itm)=>{return (itm.message)},
                ()=>{return {label: "No encontrado", value: 'default'}})
            const option = map(response.message, itm => {return {label: itm.label, value: itm._id}})
           this.setState((prevState)=>{
               return {
                   options: {...prevState.options, [this.props.name]: option}
               }


           })
    }
    }

   async componentDidMount() {
        if(this.props.type === 'selectExternalSimple' && this.props.value !== undefined){
            const endpoint = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + this.props.extra[3];
            const body = {'_id': this.props.value}
            const response =await  getFromApi(endpoint, body, this.props.token).then(
                (itm)=>{return (itm.message)},
                ()=>{return {label: "No encontrado", value: 'default'}})

       const option = map(response.message, itm => {return {label: itm.label, value: itm._id}})
       this.setState((prevState)=>{
           return {
               options: {...prevState.options, [this.props.name]: option}
           }


       })
       this.props.onChange(this.props.name, this.props.value)
    }}

    render(){


        switch(this.props.type){
            case 'selectExternalSimple': {
                return (
                    <div className="row ">
                        <input className="col" type="searchValue" name="searchInput" onChange={e=>this.handleSearchSelect(e)} />
                        <select className="col" name={this.props.name} onChange={(e)=>this.handleSelect(e)}>
                             <option>Escribe para buscar</option>
                            {map(this.state.options[this.props.name], (option)=>{
                                return (

                                    this.state.options[this.props.name] ?
                                    <option value={option.value}>{ option.label }</option> : <option value={null}>No se encontraron Opciones</option>
                                )
                            })}
                        </select>
                        <div className="col">Elegido: {map(this.state.options[this.props.name], (option)=>{return option.label})}</div>
                    </div>
                )
            }
            case 'array': {
                return <input type={this.props.type} value={this.props.value} name={this.props.name} onChange={e=>this.handleArray(e)}/>
            }
            case 'selectMultiple': {
                return <SelectMultiple type={this.props.type} value={this.props.value} name={this.props.name} onChange={this.props.onChange} extra={this.props.extra} token={this.props.token} getter={this.props.getter}/>
            }
            case 'selectMultipleCustom': {
                return <SelectMultipleCustom type={this.props.type} value={this.props.value} name={this.props.name} onChange={this.props.onChange} extra={this.props.extra} token={this.props.token} getter={this.props.getter}/>
            }
            case 'checkbox': {

                return <div className="row justify-content-start bg-body">
                          <input style={{height: '4rem', border: '0.15em solid red'}} className={"col"} type={this.props.type} checked={this.props.value} value={this.props.value } name={this.props.name} onChange={e=>this.handleCheckBox(e)}/>
                </div>
            }
            default: {
                return <input type={this.props.type} value={this.props.value} name={this.props.name} onChange={e=>this.handle(e)}/>
            }


        }
    }
}

class FormBuild extends Component {
    constructor(props) {
        super(props);
        this.state = this.props.mode === 'create' ? {} : this.props.values
    }
    handle(name, value){this.setState(()=>{return {[name]: value}})}
    onClickHandler(e){
        e.preventDefault(e)
        this.props.onClickEvent(this.state)
    }
    render(){

        return (
            <div className="row justify-content-center p-0 m-0">
                <div className="row col p-0 m-0 justify-content-center align-content-center">
                    {map(this.props.compList, (itm, i)=>{
                        return (
                            <div className="row pt-3 mt-5 " key={i}>
                                <div className="">
                                    <div className="">
                                        {itm[2]}
                                    </div>
                                </div>
                                <div className="">
                                    <div className="row">
                                        <ComponentCustom
                                            extra={itm}
                                        type={itm[0]}
                                        value={this.state[itm[1]]}
                                        name={itm[1]}
                                        onChange={(name, value)=>this.handle(name, value)}
                                            token={this.props.token}
                                            getter={(a)=>{return this.state[a]}}
                                        />
                                    </div>

                                </div>
                            </div>

                        )})}
                </div>
                <div className="row">
                    <div className="row mt-3 col justify-content-center align-content-center">
                        <div className="row col-sm-4 justify-content-center align-content-center">
                            <button onClick={(e)=>this.onClickHandler(e)} className="col btn btn-danger">{this.props.mode === 'create' ? 'Enviar' : this.props.mode === 'update' ? 'Modificar': 'Eliminar'} </button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

}


class AddNew extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }
    handlePageRestore(){
        this.props.handlePage()
        this.props.resetApi()
    }
    onClick(args){
        if(this.props.mode === 'create') {
            let payload = {
                args: args,
                modules: this.props.modules,
                mode: this.props.mode
            }
            this.props.postToApi(payload)
        } else {
            let payload = {
                args: {...args,_id:this.props.valuesUpdate._id},
                modules: this.props.modules,
                mode: this.props.mode
            }
            this.props.postToApi(payload)
        }

    }
    render() {
        return (
            <div className={"bg-dark row justify-content-start align-content-start fs-3 pt-4 m-0"}>
                {
                    this.props.cud.success ?

                        (
                            <div className="row justify-content-center align-content-center">

                                <div className="row col-12 justify-content-center align-content-center">
                                    <img src={success} alt="Success Marylin" style={{height: '30rem', width: '30rem'}}/>
                                </div>
                                <div className=" row col-3 col justify-content-center align-content-center">
                                    Exitos!
                                </div>
                                <div className=" row col justify-content-center align-content-center">
                                    <i className="bi bi-gem text-white row col justify-content-center align-content-center " style={{height: '8rem'}}></i>
                                </div>
                                <div className=" row col-sm-3 col justify-content-center align-content-center">
                                    <button className={"btn btn-danger fs-1 text-white"} onClick={(e)=>{this.props.resetApi() ;return this.props.handlePage(e)}}>Volver</button>
                                </div>
                            </div>
                        ) :

                        this.props.cud.error ? <Error404 handler={(e)=>{this.props.resetApi() ;return this.props.handlePage(e)}}/> :
                            (<FormBuild compList={this.props.form}
                                        mode={this.props.mode}
                                        values={ this.props.mode === 'create' ? {} : this.props.valuesUpdate}
                                        onClickEvent={(args)=>this.onClick(args)}
                                        token={this.props.session.token}
                                />
                            )



                }



            </div>
        );
    }
}
export default connect(
    state => ({
        session: state.SESSION,
        table: state.UMODULE,
        cud: state.UMODULECUD

    }),
    dispatch => ({
        postToApi:(payload)=> dispatch(uModuleRequestCud(payload)),
        resetApi: ()=>dispatch(resetModuleCud())
    }))(withRouter(AddNew))
