import React, {useState} from 'react';
import {connect} from "react-redux";
import {loginRequest} from "../../store/actions/common";
import logo from "../../images/img.png"
function Login({loginRequested, loginError}) {
    const [username,setUsername] = useState('');
    const [password,setPassword] = useState('')

    const callHandlerLogin = (e) =>{
        e.preventDefault()
        loginRequested(username,password)
    }
    const setPass = (event) =>{
        setPassword(event.target.value);
    }
    const setUser = (event) =>{
        setUsername(event.target.value)
    }
    return (
        <div className="container-fluid ">
            <form className="row col-12 justify-content-center align-content-center min-vh-100"  onSubmit={(e) => callHandlerLogin(e)}>
                <div className="row col-xl-7 col-sm-5 justify-content-center align-items-center ">
                    <div className="row col justify-content-center align-items-center text-white">

                        <div className="row col justify-content-center align-content-center">
                            <img className="logo bg-black spinner-border-sm" src={logo} alt="Marilyn Accesorios Logo"/>
                        </div>
                        <div className="row">
                            <input className="input-text" type="text" id="logUser" required onChange={e => setUser(e)}/>
                            <span className="floating-label">Usuario</span>
                        </div>
                        <div  className="row">
                            <input className="input-text" type="password" id="logPass" required onChange={e => setPass(e)}/>
                            <span className="floating-label">Contraseña</span>
                        </div>
                        <button className="btn btn-danger" type ="submit">Ingresar</button>
                        {loginError && (
                            <div className="container text-danger fs-1">
                                No funciono!
                            </div>
                        )}
                    </div>

                </div>
            </form>

        </div>
    );
}

export default connect(
    state => ({
        loginError: state.LOGIN.error
    }),
    dispatch =>({
        loginRequested: (username,password) => dispatch(loginRequest(username,password))
    })
)(Login);
