import { put, call } from 'redux-saga/effects';
import {
    LOGIN_FAILED,
    loginSuccess, receiveSession,
} from '../actions/common'
import UserService from "../services/user";
import User from "../model/user";


export default function* loginRequested({email, password}) {
    try {
        const backend = yield call(UserService.login, email, password);
        if(backend.token){
            const profile = yield call(UserService.decodetoken, backend.token);
            const reciveCheck = { email: email, profile: profile, page: 0}
            yield put(receiveSession(new User(reciveCheck), backend.token));
            yield put(loginSuccess());
        } else {
            yield put({type: LOGIN_FAILED})
        }
    } catch (err) {
        yield put({type: LOGIN_FAILED});
    }
}
