import {
    UMODULE_REQUEST_CUD,
    UMODULE_FAILED_CUD,
    UMODULE_SUCCESS_CUD,
    UMODULE_RESET_CUD,
} from "../actions/uModule";


export default function UMODULECUD(state =
                                         {success: false, sending: false,
                                             error: false},action) {
    switch (action.type) {
        case UMODULE_REQUEST_CUD:
            return {
                ...state, sending: true, error: false, success: false
            };
        case UMODULE_SUCCESS_CUD:
            return {
                ...state, success: true, sending: false, error: false, payload: action.payload
            };
        case UMODULE_FAILED_CUD:
            return {
                ...state, success: false,  sending: false, error: true, payload: action.payload
            }
        case UMODULE_RESET_CUD:
            return {
                ...state, success: false, sending: false, error: false, payload: null
            }

        default:
            return state;
    }
}
