import {SERVER_REQUEST, SERVER_SUCCESS, SERVER_PENDING, SERVER_FAILED} from "../actions/common";

export default function HOME(state =
                                    {success: false, sending: false,
                                        error: false},action) {
    switch (action.type) {
        case SERVER_REQUEST:
            return {
                ...state, sending: true, error: false, success: false
            };
        case SERVER_SUCCESS:
            return {
                ...state, success: true, sending: false, error: false, request: action.payload
            };
        case SERVER_FAILED:
            return {
                ...state, success: false,  sending: false, error: true
            }
        case SERVER_PENDING:
            return {
                ...state, success: false,  sending: true, error: false
            }
        default:
            return state;
    }
}
