import React, {Component} from 'react';
import {map} from "lodash";
import {connect} from "react-redux";
import {uModuleRequest} from "../../../store/actions/uModule";
import {withRouter} from "react-router-dom";


class HeaderSearch extends Component {
    constructor(props) {
        super(props);
        this.state = {
            body: {}
        }
    }



    handleSearch(){
        this.props.readTableInformationCallback(this.state.body)
    }
    handleClear(){
        this.setState({body: {}})
    }
    handleSearchInput(e){
        this.setState(prevState => {return {body: {...prevState.body,[e.target.name]:e.target.value}}})
    }
    handleSearchInputCheckBox(e){
        this.setState(prevState => {return {body:{...prevState.body, [e.target.name]: e.target.checked}}})
    }

    render() {
        const query = (trip) =>{

            switch (trip[0]){
                case 'UPDATE': return false;
                case 'DELETE': return false;
                case 'date': return false;
                case 'index': return false;

                default:{
                    switch (trip[2]){
                        case 'checkbox': {return <input checked={this.state.body[trip[0]]} name={trip[0]} className={"col-sm-6 input-text"} type={trip[2]} onChange={(e)=>this.handleSearchInputCheckBox(e)}></input>}
                        default: return <input  value={this.state.body[trip[0]] || this.props.prevState[trip[0]]} name={trip[0]} className={"col-sm-6 input-text"} type={trip[2]} onChange={(e)=>this.handleSearchInput(e)}></input>
                    }

                }
            }
        }
        return (
            <div className={"row justify-content-start bg-dark pb-5"}>
                <div className="col-12 row ">
                    <div className="col align-content-between">
                        {map(this.props.header, (triplet)=>{
                            return (
                                <div className="row col justify-content-around align-content-center fs-3 bg-dark pt-3 m-0">
                                    <div className="col-6 p-0 m-0">
                                        { query(triplet) ? triplet[1] : ''}
                                    </div>
                                    <div className="col-6  p-0 m-0 row justify-content-center ">
                                        {query(triplet)}
                                    </div>
                                </div>
                                )

                        })}
                    </div>

                </div>
                <div className="col bg-dark row align-content-center justify-content-center">
                    <div className="col row  align-content-center justify-content-center">
                        <button className="col row  fw-bold fs-6 btn btn-danger " onClick={(e)=>this.handleSearch(e)}>Buscar</button>
                    </div>
                    <div className="col row  align-content-center justify-content-center">
                        <button className="col row  fw-bold fs-6 btn btn-danger " onClick={(e)=>this.handleClear(e)}>Limpiar</button>
                    </div>
                </div>

            </div>
        );
    }
}

export default connect(
    state => ({
        table: state.UMODULE,
    }),
    dispatch => ({
        readTableInformation:( payload)=> dispatch(uModuleRequest(payload))
    })
)(withRouter(HeaderSearch))
