import React from 'react';
import {
    Card, CardBody, CardTitle, Row
} from 'reactstrap';
import HashLoader from 'react-spinners/HashLoader';

const Loading = ({label}) => (
    <div style={{padding: '30px'}}>
        <Card style={{zIndex: '999999'} } className="min-vh-100 bg-dark">
            <CardBody>
                <CardTitle>
                    <Row className="justify-content-center align-content-center min-vh-100">
                        <HashLoader
                            size={150}
                            color="#26c6da"
                            loading
                        />
                    </Row>
                    <Row style={{justifyContent: 'center'}}>
                        {label ? label : "Cargando"}
                    </Row>
                </CardTitle>
            </CardBody>
        </Card>
    </div>

);


export default Loading;
