export const AN_ERROR_OCCURRED = 'AN_ERROR_OCCURRED';
export const anErrorOccurred = error => ({type: AN_ERROR_OCCURRED, error});

export const CLEAR_ERROR = 'CLEAR_ERROR';
export const clearError = () => ({type: CLEAR_ERROR});

export const LOGIN_REQUEST = 'LOGIN_REQUEST';
export const loginRequest = (email,password) => ({type: LOGIN_REQUEST,email,password});

export const LOGIN_SUCCESS = 'LOGIN_SUCCESS';
export const loginSuccess = () => ({type: LOGIN_SUCCESS});

export const LOGIN_PENDING = 'LOGIN_PENDING';

export const LOGIN_FAILED = 'LOGIN_FAILED';
export const loginFailed = () => ({type: LOGIN_FAILED})

export const SESSION_RECEIVED = 'SESSION_RECEIVED';
export function receiveSession(profile, token) {
    return {
        type: SESSION_RECEIVED, profile, token
    };
}

export const CHANGE_PAGE_REQUEST = 'CHANGE_PAGE_REQUEST';
export const changePageRequest = (page) => ({type: CHANGE_PAGE_REQUEST,page});

export const PAGE_CHANGE_ERROR = 'PAGE_CHANGE_ERROR';
export const changePageError = () => ({type: PAGE_CHANGE_ERROR})

export const SERVER_REQUEST = 'SERVER_REQUEST';
export const serverRequest = (request) => ({type: SERVER_REQUEST, request});

export const SERVER_SUCCESS = 'SERVER_SUCCESS';
export const serverSuccess = (payload) => ({type: SERVER_SUCCESS, payload})

export const SERVER_PENDING = 'SEVER_PENDING';
export const SERVER_FAILED = 'SERVER_FAILED';
