

import { put, call, select } from 'redux-saga/effects';
import {
    uModuleSuccess
} from '../actions/uModule'
import Http from '../services/http'
export default function* uModuleRequested({payload}) {
    try {
        const {body, modules} = payload
        const skip ='?skip=' + (body.skip ? body.skip * 5 : 0)
        console.log(body, modules)
        const endpointToSearchModule = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY+ modules + '/read/'+ skip
        console.log(endpointToSearchModule, 'end')
        const session = yield select(state => state.SESSION);
        const apiCall = yield call(Http.post, endpointToSearchModule, body ? body : {}, session.token)
        if(!apiCall.isError){
                yield put(uModuleSuccess({[modules]: apiCall}))

        }
    } catch (err) {
        console.log(err)
        yield put({type: "UMODULE_FAILED"});
    }
}
