export default function* uModuleRequestedTable({args}) {

    try {
        const session = yield select(state => state.SESSION);
        const body = {
            name: session.profile.routes[session.profile.page].name, //string indicating the module table fetch
            type: 'read',
            args: args ? args : {},
            token: session.token
        }
        console.log(window.location.pathname)
        const endpointToSearchTable = process.env.REACT_APP_ENDPOINT_BACKEND_MAIN_CLOWNY + 'module/action/'
        const tableInformation = yield call(Http.post,endpointToSearchTable,body )
        console.log(tableInformation)
        if (!(tableInformation.isError)) {
            yield put(uModuleSuccessTable(tableInformation.message))
        } else {
            yield put({type: "UMODULE_FAILED_TABLE"});
        }
    }
    catch (e) {
        console.log(e)
        yield put({type: "UMODULE_FAILED_TABLE"});
    }
}
