import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './routes';
import * as serviceWorkerRegistration from './worker/serviceWorkerRegistration';
import reportWebVitals from './vitals/reportWebVitals';
import './style/style.scss';
import Root from './store/containers/Root';
import configStore from './store/config/index';
const store = configStore();
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <Root store={store} comp={App}/>
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
